import React, { useEffect, useState } from 'react';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { RefreshIcon } from '@heroicons/react/solid';

const FuelDataTable = () => {
  const [data, setData] = useState([]);
  const [fromDate, setFromDate] = useState(new Date());
  const [dropdownData, setDropdownData] = useState([]);
  const [selectedOption, setSelectedOption] = useState('');
  const [currentDate, setCurrentDate] = useState("")
  const [toDate, setToDate] = useState(null);
  const [showCurrentTable, setShowCurrentTable] = useState(true); 
  const [showHistoryTable, setShowHistoryTable] = useState(true); 
  const [loading, setLoading] = useState(false); 
  const [refreshing, setRefreshing] = useState(false);
  const [history, setHistory] = useState([]);
  const [itemsPerPage, setItemsPerPage] = useState(100);
  const [currentPage, setCurrentPage] = useState(1);
  const [historyCurrentPage, setHistoryCurrentPage] = useState(1);

  const cellClassName = 'border border-gray-300 h-16 text-xs overflow-hidden';

  const head = ["Id","Date","Time",
    "Organization Name","Location", "Vehicle Name", "Vehicle Mode", "Vehicle Model","Currentday Fuel Cost","Consumed Fuel Cost", "Sensor", "Start Fuel", "End Fuel", "Fuel Filling", "Fuel Theft", "Fuel Consumption", "Start KMS", "End KMS", "Distance Travelled", "KMPL", "Running Hours", "Engine ON Hours", "Second Engine ON Hours","Engine Idle Hours", "Liters Per Hours", "Start Location", "End Location", "Driver Name", "Driver Mobile Number", "Remark"
  ];

  const handleCurrentPageChange = (page) => {
    setCurrentPage(page);
  };
  const handleHistoryPageChange = (page) => {
    setHistoryCurrentPage(page);
  };
 // Function to fetch data from the API
  const fetchData = () => {
    setRefreshing(true); // Start the refreshing process

    axios.get('http://107.23.187.126:3000/fuel')
      .then((response) => {
        setHistory(response.data);
        const allData = response.data;
        setData(selectedOption ? allData.filter(item => item.id.toString() === selectedOption) : allData);
        const shortNames = response.data.map((item) => ({
          label: item.shortName,
          value: item.id
        }));
        setDropdownData(shortNames);
        setRefreshing(false); // Finish the refreshing process
        if (selectedOption) {
          const filtered = response.data.filter((item) => item.id.toString() === selectedOption);
          setData(filtered);
        } else {
          setData(response.data);
        }
      })
      .catch((error) => {
        if (error.response && error.response.status === 404) {
          console.error('404 Error: Resource Not Found');
        } else {
          console.error('Error fetching data: ' + error);
        }
        setRefreshing(false); // Finish the refreshing process
      });
    };

    const formatDate = (date) => {
      const year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
    
      month = month < 10 ? `0${month}` : month;
      day = day < 10 ? `0${day}` : day;
    
      return `${year}-${month}-${day}`;
    };
     
    const fetchDataFromHistoryDate = () => {
      setLoading(true);
      const formattedFromDate = fromDate ? formatDate(fromDate) : '';
      const formattedToDate = toDate ? formatDate(toDate) : '';
    
      axios
        .get(`http://107.23.187.126:3000/fuel/history/date?fromDate=${formattedFromDate}&toDate=${formattedToDate}`)
        .then((response) => {
          setLoading(false);
          if (selectedOption) {
            const filtered = response.data.filter((item) => item.id.toString() === selectedOption);
            setHistory(filtered);
          } else {
            setHistory(response.data);
            setRefreshing(false);
          }
        })
        .catch((error) => {
          console.error('Error fetching data from history table:', error);
        })
        .finally(() => {
          setRefreshing(false);
        });
    };
    

  // Function to fetch historical data
  const fetchDataFromHistory = () => {
    setLoading(true);
    
    axios
      .get(`http://107.23.187.126:3000/fuel/history`)
      .then((response) => {
        setLoading(false);
        if (selectedOption) {
          const filtered = response.data.filter((item) => item.id.toString() === selectedOption);
          setHistory(filtered);
        } else {
          setHistory(response.data);
        }
      })
      .catch((error) => {
        console.error('Error fetching data from history table:', error);
      })
      .finally(() => {
        setRefreshing(false);
      });
  };

  const handleFromDateChange = (date) => {
    setFromDate(date);
    setCurrentDate(date.toLocaleDateString());
  };

  const handleItemsPerPageChange = (event) => {
    setItemsPerPage(parseInt(event.target.value, 10)); 
  };

  useEffect(() => {
    setCurrentDate(fromDate.toLocaleDateString());
  }, [fromDate]);

  useEffect(() => {
    fetchData();
  }, [selectedOption]);

  useEffect(() => {
    fetchDataFromHistory();
  }, [selectedOption]);

  useEffect(() => {
    fetchDataFromHistoryDate();
  }, [selectedOption, fromDate, toDate]);

  const paginatedData = data.slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage);
  const paginatedHistory = history.slice((historyCurrentPage - 1) * itemsPerPage, historyCurrentPage * itemsPerPage);

  const Pagination = ({ currentPage, itemsPerPage, totalItems, onPageChange }) => {
    const totalPages = Math.ceil(totalItems / itemsPerPage);
  
    const handlePageChange = (page) => {
      if (page >= 1 && page <= totalPages) {
        onPageChange(page);
      }
    };
  
    return (
      <div className='text-center'>
        {/* Include buttons for navigating pages */}
        <button onClick={() => handlePageChange(currentPage - 1)} disabled={currentPage === 1} className='bg-sky-950 text-white border rounded m-px p-1 '>
          Previous
        </button>
        <span className=' m-px uppercase text-xs text-red-600'>{`Page ${currentPage} of ${totalPages}`}</span>
        <button onClick={() => handlePageChange(currentPage + 1)} disabled={currentPage === totalPages}  className='bg-sky-950 text-white border rounded m-px p-1 '>
          Next
        </button>
      </div>
    );
  };

  const renderTable = () => {
    if (showCurrentTable && showHistoryTable) {

      return ( 
        <>
        <div>
        <h2 className='text-2xl uppercase text-sky-950 text-center font-semibold'>Live Table</h2>
        <Pagination
              currentPage={currentPage}
              itemsPerPage={itemsPerPage}
              totalItems={data.length}
              onPageChange={handleCurrentPageChange}
            />
        <table className='border-2 border-black p-1 w-full'>
        <thead>
        <tr>
                        {head.map((heading, index) => (
                              <th
                              key={index}
                              className={`px-6 py-3 bg-sky-950 text-center text-white text-xs font-semibold border text-black-700 uppercase tracking-wider
                              `}
                            >
                                {heading}
                            </th>
                        ))}
                    </tr>
        </thead>
        <tbody >
        {paginatedData.map((item, rowIndex) => (
            
            <tr key={rowIndex} className={`${rowIndex % 2 === 0 ? 'bg-white' : 'bg-gray-200'} text-center cursor-pointer`}>
                   <td className={`${cellClassName}`}>{item.id}</td>
                   <td className={`${cellClassName}`}>{item.time.substring(0, 10)}</td>
                   <td className={`${cellClassName}`}>{item.time.substring(11, 19)}</td>
                  <td className={`${cellClassName}`}>{item.orgId}</td>
                  <td className={`${cellClassName}`}>
                <a 
                  href={`https://www.google.com/maps?q=${item.latitude},${item.longitude}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-blue-500"
                >
                 Link
                </a>
              </td>
                  <td className={`${cellClassName}`}>{item.shortName}</td>
                  <td className={`${cellClassName}`}>{item.vehicleMode}</td>
                  <td className={`${cellClassName}`}>{item.vehicleModel}</td>
                  <td className={`${cellClassName}`}>{item.currentdayfuelcost}</td>
                  <td className={`${cellClassName}`}>{item.consumedfuelcost}</td>
                  <td className={`${cellClassName}`}>{item.sensor}</td>
                  <td className={`${cellClassName}`}>{item.fuelLitre}</td>
                  <td className={`${cellClassName}`}>{item.end_fuel}</td>
                  <td className={`${cellClassName}`}>{item.fuel_filling}</td>
                  <td className={`${cellClassName}`}>{item.fuel_theft}</td>
                  <td className={`${cellClassName}`}>{item.fuel_consumption}</td>
                  <td className={`${cellClassName}`}>{item.odoDistance}</td>
                  <td className={`${cellClassName}`}>{item.end_kms}</td>
                  <td className={`${cellClassName}`}>{item.distance_travelled}</td>
                  <td className={`${cellClassName}`}>{item.kmpl}</td>
                  <td className={`${cellClassName}`}>{item.todayWorkingHours}</td>
                  <td className={`${cellClassName}`}>{item.todayWorkingHours}</td>
                  <td className={`${cellClassName}`}>{item.secondaryEngineHours}</td>
                  <td className={`${cellClassName}`}>{item.idleTime}</td>
                  <td className={`${cellClassName}`}>{item.liters_per_hour}</td>
                  <td className={`${cellClassName} min-w-[200px]`}>{item.address}</td>
                  <td className={`${cellClassName}`}>{item.end_location}</td>
                  <td className={`${cellClassName}`}>{item.driverName}</td>
                  <td className={`${cellClassName}`}>{item.driverMobile}</td>
                  <td className={`${cellClassName}`}>{item.remarks}</td>
            </tr>
          ))}
        </tbody>
      </table>
      
      </div>
    
        <div>
      <h2 className='text-2xl uppercase text-sky-950 text-center font-semibold'>History Table</h2>
      <Pagination
              currentPage={historyCurrentPage}
              itemsPerPage={itemsPerPage}
              totalItems={history.length}
              onPageChange={handleHistoryPageChange}
            />
        <table  className='border-2 border-black'>
        
        <thead>
        <tr>
                        {head.map((heading, index) => (
                            <th
                                key={index}
                                className="px-6 py-3 bg-sky-950 text-center text-white text-xs border font-semibold text-black-700 uppercase tracking-wider "
                            >
                                {heading}
                            </th>
                        ))}
                    </tr>
        </thead>
        <tbody className='text-center'>
          {paginatedHistory.map((item , rowIndex) => (
            <tr key={rowIndex} className={`${rowIndex % 2 === 0 ? 'bg-white' : 'bg-gray-200'}  w-[6000px], cursor-pointer`}>
               <td className={`${cellClassName}`}>{item.id}</td>
               <td className={`${cellClassName}`}>{item.time.substring(0, 10)}</td>
               <td className={`${cellClassName}`}>{item.time.substring(11, 19)}</td>
                  <td className={`${cellClassName}`}>{item.orgId}</td>
                  <td className={`${cellClassName}`}>
                <a 
                  href={`https://www.google.com/maps?q=${item.latitude},${item.longitude}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-blue-500"
                >
                 Link
                </a>
              </td>
                  <td className={`${cellClassName}`}>{item.shortName}</td>
                  <td className={`${cellClassName}`}>{item.vehicleMode}</td>
                  <td className={`${cellClassName}`}>{item.vehicleModel}</td>
                  <td className={`${cellClassName}`}>{item.currentdayfuelcost}</td>
                  <td className={`${cellClassName}`}>{item.consumedfuelcost}</td>
                  <td className={`${cellClassName}`}>{item.sensor}</td>
                  <td className={`${cellClassName}`}>{item.fuelLitre}</td>
                  <td className={`${cellClassName}`}>{item.end_fuel}</td>
                  <td className={`${cellClassName}`}>{item.fuel_filling}</td>
                  <td className={`${cellClassName}`}>{item.fuel_theft}</td>
                  <td className={`${cellClassName}`}>{item.fuel_consumption}</td>
                  <td className={`${cellClassName}`}>{item.odoDistance}</td>
                  <td className={`${cellClassName}`}>{item.end_kms}</td>
                  <td className={`${cellClassName}`}>{item.distance_travelled}</td>
                  <td className={`${cellClassName}`}>{item.kmpl}</td>
                  <td className={`${cellClassName}`}>{item.todayWorkingHours}</td>
                  <td className={`${cellClassName}`}>{item.todayWorkingHours}</td>
                  <td className={`${cellClassName}`}>{item.secondaryEngineHours}</td>
                  <td className={`${cellClassName}`}>{item.idleTime}</td>
                  <td className={`${cellClassName}`}>{item.liters_per_hour}</td>
                  <td className={`${cellClassName} min-w-[200px]`}>{item.address}</td>
                  <td className={`${cellClassName}`}>{item.end_location}</td>
                  <td className={`${cellClassName}`}>{item.driverName}</td>
                  <td className={`${cellClassName}`}>{item.driverMobile}</td>
                  <td className={`${cellClassName}`}>{item.remarks}</td>
            </tr>
          ))}
        </tbody>
      </table>
     
      </div>
      </>
      );
  };
  return null;
  };
   


  const handleRefresh = () => {
    setRefreshing(true); // Start the refreshing process

    if (showCurrentTable) {
      // If the current data table is shown, refresh current data
      fetchData();
    } else {
      // If historical data is shown, refresh historical data
      fetchDataFromHistory();
    }
  };

  return (
    <div className="flex flex-col items-start w-full">
     <div className="flex items-center  w-full">
  <div className="w-full bg-gray-200 p-4 flex  items-center justify-between">
    <div>
      <button
        className="bg-sky-900 hover-bg-sky-950 text-white font-bold py-2 px-4 rounded w-32"
        onClick={handleRefresh}
        disabled={refreshing}
      >
        {refreshing ? (
          <div className="flex items-center">
            <div className="w-4 h-4 mr-2 animate-spin">
              <RefreshIcon className="w-full h-full text-white animate-spin" />
            </div>
            Refresh..
          </div>
        ) : (
          <div className="flex items-center">
            <div className="w-4 h-4 mr-2">
              <RefreshIcon className="w-full h-full text-white" />
            </div>
            Refresh
          </div>
        )}
      </button>
    </div>

    
      <label className="mr-2">Vehicle No:</label>
      {dropdownData && dropdownData.length > 0 && (
        <select
          value={selectedOption}
          onChange={(event) => {
            setSelectedOption(event.target.value);
            setShowCurrentTable(true);
            setShowHistoryTable(true);
          }}
          className="py-2 px-2 border border-black-300 text-black rounded-lg cursor-pointer"
        >
          <option value="" className="text-black">
            Select
          </option>
          {dropdownData.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </select>
      )}
      {(!dropdownData || dropdownData.length === 0) && (
        <span>Loading..</span>
      )}

<select
            value={itemsPerPage}
            onChange={handleItemsPerPageChange}
            className="py-2 px-1 ml-[10px] border border-black-300 text-black rounded-lg cursor-pointer"
          >            
            <option value="10">10</option>
            <option value="20">20</option>
            <option value="50">50</option>
            <option value="100">100</option>
          </select>

      <label className="mx-2">From:</label>
      <DatePicker
        selected={fromDate}
        onChange={handleFromDateChange}
        className="py-2 px-2 border border-black-300 rounded-lg cursor-pointer placeholder-red-500 placeholder-opacity-80"
      />
      <label className="mx-2">To:</label>
      <DatePicker
        selected={toDate}
        onChange={(date) => setToDate(date)}
        className="py-2 px-2 border border-black-300 rounded-lg cursor-pointer placeholder-red-500 placeholder-opacity-80"
      />
      <button
        onClick={fetchDataFromHistory}
        className="bg-sky-900 hover-bg-sky-950 text-white  font-bold py-2 px-4 rounded w-32"
        disabled={loading}
      >
        {loading ? (
          <div className="flex items-center">
            <div className="w-4 h-4 mr-2 animate-spin">
              <RefreshIcon className="w-full h-full text-white animate-spin" />
            </div>
            Loading...
          </div>
        ) : (
          <div className="flex items-center">
            <div className="w-4 h-4 mr-2">
              <RefreshIcon className="w-full h-full text-white" />
            </div>
            Submit
          </div>
        )}
      </button>
    </div>
  </div>



      <div className='max-h-[500px] max-w-[100%] overflow-auto'>{renderTable()}</div>
    </div>
  );
};

export default FuelDataTable;